            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.donationEntry{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    height: 65px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background: #36444e;
}
.donationEntry:hover{
    background: #2e3942;
}
.donationHead{
    background: #1e252b;
    pointer-events: none;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 65px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.donationHead p{
    color: #fff;
}
.donationCell{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    width: 200px;
}
.donationCell p{
    color: #fff;
}
.welcomeText{
    font-size: 42px;
    font-weight: bold;
    color: #1e252b;
}
.generalButton{
    background: #cc0033;
    padding: 20px;
    width: 200px;
    color: #fff;
    font-size: 22px;
    margin-left: 25px;
    margin-right: 25px;
    border: none;
    border-radius: 10px;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.generalButton:hover{
    background: #b5002d;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}
.spacer{
